import React, { useState } from "react";
import axios from "axios";
import "./TournamentRegistration.scss";
import Solo from "../../assets/images/solo.png";
import Team from "../../assets/images/team.png";

const TournamentRegistration = () => {
    const [step, setStep] = useState(1);
    const [formData, setFormData] = useState({
        teamLogo: null,
        teamName: '',
        personalData: {
            name: "",
            email: "",
            phone: "",
            telegram: ""
        },
        teamData: {
            name: "",
            members: Array(7).fill({ name: "", telegram: "" })
        }
    });

    const handlePersonalDataChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevData => ({
            ...prevData,
            personalData: {
                ...prevData.personalData,
                [name]: value
            }
        }));
    };

    const handleTeamDataChange = (index, field, value) => {
        setFormData(prevData => {
            const updatedMembers = [...prevData.teamData.members];
            updatedMembers[index] = {
                ...updatedMembers[index],
                [field]: value
            };
            return {
                ...prevData,
                teamData: {
                    ...prevData.teamData,
                    members: updatedMembers
                }
            };
        });
    };

    const handleTeamName = (event) => {
        const { value } = event.target;
        setFormData(prevData => ({
            ...prevData,
            teamName: value
        }));
    };

    const handleFileUpload = (event) => {
        const file = event.target.files[0];
        const reader = new FileReader();
        reader.onloadend = () => {
            setFormData(prevData => ({
                ...prevData,
                teamLogo: reader.result // reader.result   base64  
            }));
        };
        reader.readAsDataURL(file);
    };

    const nextStep = () => {
        setStep(prevStep => prevStep + 1);
    };

    const submitData = async () => {
        try {
            const response = await fetch('https://mytestjson1.s3.eu-north-1.amazonaws.com/registration.json');
            const existingData = await response.json();
            
            // Append new formData to existing data
            existingData.push(formData);
            
            // Send the updated data back to the server
            const updateResponse = await fetch('https://mytestjson1.s3.eu-north-1.amazonaws.com/registration.json', {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(existingData)
            });
            
            if (updateResponse.ok) {
                alert("Data submitted successfully!");
            } else {
                alert("Failed to submit data.");
            }
        } catch (error) {
            console.error("Error submitting data", error);
            alert("Failed to submit data.");
        }
    };

    return (
        <div className="tournamentRegistration_main">
            {step === 1 && (
                <div className="team_or_solo_wrapper">
                    <div className="team_or_solo" onClick={nextStep}>
                        <span> /</span>
                        <img src={Solo} alt="Solo" />
                    </div>
                    <div className="team_or_solo" onClick={nextStep}>
                        <span> </span>
                        <img src={Team} alt="Team" />
                    </div>
                </div>
            )}

            {step === 2 && (
                <div className="personal_data_wrapper">
                    <p style={{color: "#FAFF00", fontSize: "3vw", fontWeight: "bold"}}> !</p>
                    <div className="personal_data">
                        <p style={{color: "#FFFFFF", fontSize: "2vw", fontWeight: "bold"}}> :</p>
                        <form className="personal_data_form">
                            <div className="personal_data_form_div_phone">
                                <div>
                                    <span>  ':</span>
                                    <input
                                    placeholder=" , '"
                                    name="name"
                                    value={formData.personalData.name}
                                    onChange={handlePersonalDataChange}
                                />
                                </div>
                                <div>
                                    <span>Email:</span>
                                    <input
                                        placeholder=" email"
                                        name="email"
                                        value={formData.personalData.email}
                                        onChange={handlePersonalDataChange}
                                    />
                                </div>
                                <div>
                                    <span> :</span>
                                    <input
                                        placeholder="  "
                                        name="phone"
                                        value={formData.personalData.phone}
                                        onChange={handlePersonalDataChange}
                                    />
                                </div>
                                <div>
                                    <span>Telegram username:</span>
                                    <input
                                        placeholder="@username"
                                        name="telegram"
                                        value={formData.personalData.telegram}
                                        onChange={handlePersonalDataChange}
                                    />
                                </div>
                            </div>
                            <div className="personal_data_form_div">
                                <span>  ':</span>
                                <span>Email:</span>
                                <span> :</span>
                                <span>Telegram username:</span>
                            </div>
                            <div className="inputs personal_data_form_div">
                                <input
                                    placeholder=" , '"
                                    name="name"
                                    value={formData.personalData.name}
                                    onChange={handlePersonalDataChange}
                                />
                                <input
                                    placeholder=" email"
                                    name="email"
                                    value={formData.personalData.email}
                                    onChange={handlePersonalDataChange}
                                />
                                <input
                                    placeholder="  "
                                    name="phone"
                                    value={formData.personalData.phone}
                                    onChange={handlePersonalDataChange}
                                />
                                <input
                                    placeholder="@username"
                                    name="telegram"
                                    value={formData.personalData.telegram}
                                    onChange={handlePersonalDataChange}
                                />
                            </div>
                        </form>
                        <button type="button" className="continue" onClick={nextStep}></button>
                    </div>
                </div>
            )}

            {step === 3 && (
                <div className="team_data_wrapper">
                    <p style={{color: "#FAFF00", fontSize: "3vw", fontWeight: "bold"}}> !</p>
                    <div className="team_data">
                        <p style={{color: "#FFFFFF", fontSize: "2vw", fontWeight: "bold"}}> :</p>
                        <form className="personal_data_form">

                            <div className="team_data_div_phone">
                                <div style={{width: "60%"}}>
                                    <div>
                                        <span className="data_span"> </span>
                                        <div className="description">   ,    </div>
                                    </div>
                                    <input type="file" onChange={handleFileUpload} />
                                </div>

                               
                                    
                                <div style={{width: "60%"}}>
                                    <span className="data_span"> :</span>
                                    <div className="description"> ,   - , .  ,          .     </div>
                                    <input
                                        onChange={handleTeamName}
                                        style={{ display: "inline-block", backgroundColor: "#191919", color: "#FFFFFF", borderRadius: "5px", height: "30px" }}
                                        value={formData.teamName}
                                    />
                                </div>

                                <div style={{width: "100%"}}>
                                    <div>
                                        <span className="data_span">  :</span>
                                        <div className="description"> ,    .   2   .</div>
                                    </div>
                                    <div className="team_members_names_and_telegram_div">
                                        <div className="team_name_surname">
                                            <span style={{ width: "200px" }}>  '</span>
                                            <span style={{ width: "250px" }}>Telegram @username</span>
                                        </div>
                                        <div style={{ display: "flex", justifyContent: "space-around" }}>
                                            <div style={{ display: "flex", flexDirection: "column", gap: "30px" }}>
                                                <span className="data_span"> 1</span>
                                                <span className="data_span"> 2</span>
                                                <span className="data_span"> 3</span>
                                                <span className="data_span"> 4</span>
                                                <span className="data_span"> 5</span>
                                                <span className="data_span"> 6</span>
                                                <span className="data_span"> 7</span>
                                            </div>
                                            <div className="member_line">
                                                {formData.teamData.members.map((member, index) => (
                                                    <div key={index}>
                                                        <input
                                                            style={{ marginRight: "30px" }}
                                                            placeholder={` ${index + 1} '`}
                                                            value={member.name}
                                                            onChange={(e) => handleTeamDataChange(index, 'name', e.target.value)}
                                                        />
                                                        <input
                                                            placeholder={` ${index + 1} Telegram`}
                                                            value={member.telegram}
                                                            onChange={(e) => handleTeamDataChange(index, 'telegram', e.target.value)}
                                                        />
                                                    </div>
                                                ))}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                

                            </div>

                            <div className="labels">
                                <div>
                                    <span> </span>
                                    <div className="description">   ,    </div>
                                </div>
                                <div>
                                    <span> :</span>
                                    <div className="description"> ,   - , .  ,          .     </div>
                                </div>
                                <hr style={{width: "300%"}} />
                                <div>
                                    <span>  :</span>
                                    <div className="description"> ,    .   2   .</div>
                                </div>
                            </div>
                            <div className="team_inputs">
                                <input type="file" onChange={handleFileUpload} />
                                {formData.teamLogo && <img src={formData.teamLogo} alt="Team Logo" style={{ width: '100px', height: '100px', marginTop: '10px' }} />}
                                <input
                                    onChange={handleTeamName}
                                    style={{ display: "inline-block", backgroundColor: "#191919", color: "#FFFFFF", borderRadius: "5px", height: "30px" }}
                                    value={formData.teamName}
                                />
                                <div className="team_members_names_and_telegram_div">
                                    <div className="team_name_surname">
                                        <span style={{ width: "200px" }}>  '</span>
                                        <span style={{ width: "250px" }}>Telegram @username</span>
                                    </div>
                                    <div style={{ display: "flex", justifyContent: "space-around" }}>
                                        <div style={{ display: "flex", flexDirection: "column", gap: "30px" }}>
                                            <span> 1, </span>
                                            <span> 2</span>
                                            <span> 3</span>
                                            <span> 4</span>
                                            <span> 5</span>
                                            <span> 6</span>
                                            <span> 7</span>
                                        </div>
                                        <div className="member_line">
                                            {formData.teamData.members.map((member, index) => (
                                                <div key={index}>
                                                    <input
                                                        style={{ marginRight: "30px" }}
                                                        placeholder={` ${index + 1} '`}
                                                        value={member.name}
                                                        onChange={(e) => handleTeamDataChange(index, 'name', e.target.value)}
                                                    />
                                                    <input
                                                        placeholder={` ${index + 1} Telegram`}
                                                        value={member.telegram}
                                                        onChange={(e) => handleTeamDataChange(index, 'telegram', e.target.value)}
                                                    />
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>
                        <button type="button" className="continue" onClick={nextStep}></button>
                    </div>
                </div>
            )}

            {step === 4 && (
                <div className="payment_wrapper">
                    <p style={{color: "#FAFF00", fontSize: "3vw", fontWeight: "bold"}}> !</p>
                    <div className="payment">
                        <p className="payment_title"></p>
                        <div>
                            <ol type="1" className="ol_payment">
                                <li>    ?     - .</li>
                                <li>         - 500 ( )  : 5375414144578654.      . </li>
                                <li>        -  </li>
                            </ol>
                            <p className="have_mistrust">   ?    ,        .</p>
                        </div>
                        <button type="button" className="continue" onClick={() => { submitData(); nextStep(); }}> . </button>
                    </div>
                </div>
            )}

            {step === 5 && (
                <div className="lastStep_wrapper">
                    <p style={{color: "#FAFF00", fontSize: "3vw", fontWeight: "bold"}}> !</p>
                    <div className="lastStep">
                        <p className="payment_title"> </p>
                        <div>
                            <p className="payment_title">'    ,   </p>
                            <ol type="1" className="ol_payment">
                                <li>   </li>
                                <li>    </li>
                            </ol>
                        </div>
                        <button type="button" className="continue" onClick={() => window.open('https://t.me/knappteam_cyberche', '_blank')}>   Telegram</button>
                    </div>
                </div>
            )}
        </div>
    );
}

export default TournamentRegistration;


